import {Dialog, DialogBody, DialogHeader, Text, TextArea} from "@gravity-ui/uikit";
import xmlFormat from 'xml-formatter';

export default function ViewSendRequest(props) {

    const formatedData = () =>{
        if (props?.rowData.reqRow){
            return xmlFormat(props?.rowData.reqRow)
        }
    }

    return (
        <>
            <Dialog size={"l"} type={""} open={props.open} onClose={() => props.setOpen(false)}>
                <DialogHeader caption={"ID: " + props.rowData.id}/>
                <DialogBody>
                    <TextArea maxRows={40} defaultValue={formatedData()}></TextArea>
                </DialogBody>

            </Dialog>
        </>
    )
}