import {
    Button,
    Dialog,
    DialogBody,
    DialogHeader,
    Flex,
    Icon,
    Text,
    TextInput,
    ToasterComponent,
    useToaster
} from "@gravity-ui/uikit";
import {Field, Form} from "react-final-form";
import {FloppyDisk} from "@gravity-ui/icons";
import axios from "axios";
import {UrlProperty} from "../../config/UrlProperty";

export default function VsAddNew(props) {
    const {add} = useToaster();
    const addVs = (data) => {
        axios.post(UrlProperty().addVs, {}, {
            params: {
                root_tag: data.rootTag,
                mnemonic: data.mnemonic,
                mnemonicDesc: data?.mnemonicDesc,
                vsDesc: data?.vsDesc
            }
        }).then(r => {
            viewSuccess(r.data);
            props.setOpen(false);
        }).catch(e => {
            console.log(e);
            viewError("Failed add VS", e.response.data)
        })
    }
    const onSubmit = (data) => {
        if (data?.rootTag?.length > 0 && data?.mnemonic) {
            addVs(data);
        } else {
            viewError("Error add new VS", "root tag or mnemonic is empty")
        }
    }
    const viewError = (t, m) => {
        add({
            theme: "danger",
            title: t, content: m
        })
    }

    const viewSuccess = (m) => {
        add({
            theme: "success",
            title: "Success", content: m
        })
    }
    return (
        <>
            <ToasterComponent className="optional additional classes"/>
            <Dialog size={"l"} type={""} open={props.open} onClose={() => props.setOpen(false)}>
                <DialogHeader caption={"Добавить ВС"}/>
                <DialogBody>
                    <Form subscription={{submitting: true}} onSubmit={onSubmit}
                          render={({handleSubmit}) => (
                              <form onSubmit={handleSubmit}>
                                  <Field name={"rootTag"}>
                                      {props => (
                                          <Flex alignItems={"center"} gap={{s: 1, m: 6}}>
                                              <Text variant="body-1" size={"s"} theme={"clear"}>RootElementLocalName</Text>
                                              <TextInput
                                                         id={"textRootTag"}
                                                         name={props?.input.name}
                                                         value={props.input.value}
                                                         onChange={props?.input.onChange}
                                                         placeholder={"InquiryDocumentsRequest"}></TextInput>
                                          </Flex>
                                      )}
                                  </Field>

                                  <Field name={"vsDesc"}>
                                      {props => (
                                          <Flex alignItems={"center"} gap={{s: 1, m: 6}}>
                                              <Text  whiteSpace={"nowrap"} variant="body-1" size={"s"} theme={"clear"}>Краткое описание</Text>
                                              <TextInput
                                                         id={"textRootTagDesc"}
                                                         name={props?.input.name}
                                                         value={props.input.value}
                                                         onChange={props?.input.onChange}
                                                         placeholder={"Запоросы СПИ"}></TextInput>
                                          </Flex>
                                      )}
                                  </Field>

                                  <Field name={"mnemonic"}>
                                      {props => (
                                          <Flex alignItems={"center"} gap={{s: 1, m: 6}}>
                                              <Text whiteSpace={"nowrap"} variant="body-1" size={"s"} theme={"clear"}>Мнемоника отправителя</Text>
                                              <TextInput
                                                         id={"textMnemonic"}
                                                         name={props?.input.name}
                                                         value={props.input.value}
                                                         onChange={props?.input.onChange}
                                                         placeholder={"FS0001"}></TextInput>
                                          </Flex>
                                      )}
                                  </Field>

                                  <Field name={"mnemonicDesc"}>
                                      {props => (
                                          <Flex alignItems={"center"} gap={{s: 1, m: 6}}>
                                              <Text whiteSpace={"nowrap"} variant="body-1" >Описание мнемоники отправителя</Text>
                                              <TextInput
                                                         id={"textMnemonicDesc"}
                                                         name={props?.input.name}
                                                         value={props.input.value}
                                                         onChange={props?.input.onChange}
                                                         placeholder={"АИС БЛА БЛА"}></TextInput>
                                          </Flex>
                                      )}
                                  </Field>
                                  <br></br>
                                  <Button view={"outlined-info"} pin={"circle-circle"} size={"s"} type={"submit"}>
                                      <Icon data={FloppyDisk} size={18}/>
                                      Создать</Button>

                              </form>
                          )}
                    /></DialogBody>
            </Dialog>
        </>
    )
}