import {
    Button,
    Checkbox,
    Dialog,
    DialogBody,
    DialogHeader,
    Flex,
    Icon,
    Label,
    Loader,
    RadioButton,
    TextArea,
    TextInput,
    ToasterComponent,
    useToaster
} from "@gravity-ui/uikit";
import {createRef, useEffect, useState} from "react";
import {CirclePlus} from "@gravity-ui/icons";
import {Field, Form} from "react-final-form";
import ViewVs from "../vs/ViewVs";
import {Base64} from "js-base64";
import axios from "axios";
import {UrlProperty} from "../../config/UrlProperty";

export default function NewRequest() {
    const [isLoaded, setIsLoaded] = useState(false);
    const [pageVersion, setPageVersion] = useState(10);
    const [contentFile, setContentFile] = useState("");
    let inputContentFile = createRef();

    const [onDownloadContentFile, setOnDownloadContentFile] = useState(false);
    const [openSelectTag, setOpenSelectTag] = useState(false);
    const [rootTagSelected, setRootTagSelected] = useState("");

    const [attachFiles, setAttachFiles] = useState([])
    const addFiles = createRef();

    const {add} = useToaster();
    useEffect(() => {
        updatePageVersion();
    }, [isLoaded])
    const openAttachFile = () => {
        addFiles.current.click();
    }
    const openContentFile = () => {
        inputContentFile.current.click();
    }
    const updatePageVersion = () => {
        let v = pageVersion + 1;
        setPageVersion(v)
    }

    const appendAttach = async (e, name, isBinary) => {
        if (e) {
            setIsLoaded(true);
            e.preventDefault()
            const reader = new FileReader()
            reader.onload = async (e) => {
                const text = (e.target.result)
                let list = attachFiles;
                let file;

                if (isBinary === true) {
                    file = {
                        fileName: name?.length > 0 ? name : "attachment_z_" + list.length + 1,
                        base64ByteArray: btoa(String.fromCharCode.apply(null, new Uint8Array(text)))
                    }
                } else {
                    file = {
                        fileName: name?.length > 0 ? name : "attachment_s_" + list.length + 1,
                        base64String: btoa(String.fromCharCode.apply(null, new Uint8Array(text)))
                    }
                }

                list.push(file)
                setAttachFiles(list);
            };
            reader.readAsArrayBuffer(e.target.files[0])
            setIsLoaded(false);

        }
        updatePageVersion();
    }

    const showFile = async (e) => {
        if (e) {
            setIsLoaded(true);
            e.preventDefault()
            const reader = new FileReader()
            reader.onload = async (e) => {
                const text = (e.target.result)
                setContentFile(btoa(String.fromCharCode.apply(null, new Uint8Array(text))))
            };
            reader.readAsArrayBuffer(e.target.files[0])
            setIsLoaded(false);
        }
    }

    const writeContentFileModal = () => {
        function onSubmit(e) {
            setContentFile(Base64.encode(e?.content));
            setOnDownloadContentFile(false);
        }

        const contentExample =
            "<fssp:InquiryDocumentsRequest xmlns:att=\"urn://x-artifacts-fssp-ru/mvv/smev3/attachments/1.1.0\" xmlns:c=\"urn://x-artifacts-fssp-ru/mvv/smev3/container/1.1.0\" xmlns:fssp=\"urn://x-artifacts-fssp-ru/mvv/smev3/inquiry-documents/1.1.1\">\n" +
            "\t<c:ID>11111111111111</c:ID>\n" +
            "\t<c:Date>2023-12-09T19:20:22</c:Date>\n" +
            "\t<c:SenderID>11111111</c:SenderID>\n" +
            "\t<c:SenderDepartmentCode>11111</c:SenderDepartmentCode>\n" +
            "\t<c:ReceiverID>111111</c:ReceiverID>\n" +
            "</fssp:InquiryDocumentsRequest>";

        return (
            <Dialog size={"l"} type={""} open={onDownloadContentFile} onClose={() => setOnDownloadContentFile(false)}>
                <DialogHeader caption={"Content"}/>
                <DialogBody>

                    <Form subscription={{submitting: true}} onSubmit={onSubmit}
                          render={({handleSubmit}) => (
                              <form onSubmit={handleSubmit}>
                                  <Field name={"content"}>
                                      {props => (
                                          <Flex alignItems={"center"} gap={{s: 1, m: 6}}>
                                              <TextArea
                                                  rows={25}
                                                  id={"content"}
                                                  name={props?.input.name}
                                                  value={props.input.value}
                                                  onChange={props?.input.onChange}
                                                  placeholder={contentExample}></TextArea>
                                          </Flex>
                                      )}
                                  </Field>
                                  <br/>
                                  <Button view={"outlined-info"} pin={"circle-circle"} size={"s"} type={"submit"}>
                                      <Icon data={CirclePlus} size={18}/>
                                      Добавить</Button>
                              </form>
                          )}
                    />

                </DialogBody>
            </Dialog>
        )
    }

    const pushRequest = (b, u) => {
        let url = u === "GetRequest" ? UrlProperty().createRequest : UrlProperty().createResponse;
        let body = {}
        axios.post(url, b).then(
            r => {
                add({
                    theme: "success",
                    title: "Success", content: ``
                })
            }
        ).catch(e => {
            add({
                theme: "danger",
                title: "Failed add request", content: e.response.data
            })
        })
    }

    const selectRootTag = () => {
        return (
            <Dialog size={"l"} type={""} open={openSelectTag} onClose={() => setOpenSelectTag(false)}>
                <DialogHeader caption={"Root Element Local Name"}/>
                <DialogBody>
                    <ViewVs
                        rootTagSelected={rootTagSelected}
                        setRootTagSelected={setRootTagSelected}
                        setOpenSelectTag={setOpenSelectTag}
                    />
                </DialogBody>
            </Dialog>
        )

    }

    const attachList = () => {
        let lables = []
        let i = pageVersion;
        attachFiles.forEach(
            (a) => {
                lables.push(
                    <>{"  "}
                        <Label theme={"info"} type={"close"} key={i} onCloseClick={() => {
                            if (window.confirm("Удалить вложение (удаляется по названию файла)")) {
                                let n = []
                                attachFiles.forEach(x => {
                                    if (x.fileName !== a?.fileName) {
                                        n.push(x);
                                    }
                                })
                                setAttachFiles(n);
                                i = i++;
                            }
                        }}>{a?.fileName}</Label>
                    </>
                )
                i++;
            }
        )
        return lables;
    }

    const onSubmit = (e) => {
        let request = {
            rootTag: rootTagSelected,
            msgId: e?.msgId,
            originalMsgId: e?.originalMsgId,
            content: contentFile,
            attachments: attachFiles
        }
        console.log(request);
        console.log(e)
        pushRequest(request, e?.request_type);
    }
    const [openDialogAddAttach, setOpenDialogSetAttach] = useState(false);
    const addAttach = () => {
        let fileName;
        let isBinary;

        function submit(d) {
            fileName = d?.attach_name;
            isBinary = d?.is_binary;
        }

        return (
            <>
                <Dialog size={"s"} onClose={() => setOpenDialogSetAttach(false)} open={openDialogAddAttach}>
                    <DialogHeader caption={"Добавить вложение"}/>
                    <DialogBody>
                        <Form subscription={{submitting: true}} onSubmit={submit}
                              render={({handleSubmit}) => (
                                  <form onSubmit={handleSubmit} key={pageVersion}>
                                      <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                            gap={{s: 6, m: 6}}>
                                          <Field name={"attach_name"}>
                                              {props => (
                                                  <>
                                                      <TextInput
                                                          id={"attach_name"}
                                                          name={props?.input.name}
                                                          value={props.input.value}
                                                          onChange={props?.input.onChange}
                                                          hasClear={true}
                                                          label={"Название файла вложение"}></TextInput>

                                                  </>
                                              )}
                                          </Field>
                                      </Flex>
                                      <br/>

                                      <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                            gap={{s: 6, m: 6}}>
                                          <Field name={"is_binary"}>
                                              {props => (
                                                  <>
                                                      <Checkbox
                                                          id={"is_binary"}
                                                          name={props?.input.name}
                                                          // value={props.input.value}
                                                          onChange={props?.input.onChange}
                                                          hasClear={true}
                                                          content={"бинарный (zip архив)"}
                                                      ></Checkbox>

                                                  </>
                                              )}
                                          </Field>
                                      </Flex>
                                      <br/>

                                      <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                            gap={{s: 10, m: 6}}>
                                          <Field name={"rootTag"}>
                                              {props => (
                                                  <>
                                                      <input
                                                          style={{display: 'none'}}
                                                          ref={addFiles}
                                                          type="file"
                                                          onChange={(e) => appendAttach(e, fileName, isBinary)}
                                                      />
                                                      <Button
                                                          type={"submit"}
                                                          onClick={openAttachFile}>Выбрать файл</Button>

                                                  </>
                                              )}
                                          </Field>
                                      </Flex>
                                      <br/>
                                  </form>
                              )}/>
                    </DialogBody>
                </Dialog>
            </>
        )
    }


    return (
        <div key={pageVersion}>
            <ToasterComponent className="optional additional classes"/>
            {writeContentFileModal()}
            {selectRootTag()}
            {addAttach()}

            {isLoaded && (
                <>
                    <Loader size={"l"}/>
                </>
            ) || (
                <Form subscription={{submitting: true}} onSubmit={onSubmit}
                      render={({handleSubmit}) => (
                          <form onSubmit={handleSubmit} key={pageVersion}>
                              <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                    gap={{s: 10, m: 6}}>
                                  <Field name={"request_type"}
                                         defaultValue={"GetRequest"}
                                  >
                                      {props => (
                                          <>
                                              <RadioButton
                                                  id={"request_type"}
                                                  name={props?.input.name}
                                                  onChange={props?.input.onChange}
                                                  options={[
                                                      {value: 'GetRequest', content: 'GetRequest'},
                                                      {value: 'GetResponse', content: 'GetResponse'}]
                                                  }
                                                  width={"max"}
                                              />
                                          </>
                                      )}
                                  </Field>
                              </Flex>
                              <br/>


                              <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                    gap={{s: 10, m: 6}}>
                                  <Field name={"msgId"}>
                                      {props => (
                                          <>
                                              <TextInput
                                                  id={"msgId"}
                                                  name={props?.input.name}
                                                  value={props.input.value}
                                                  onChange={props?.input.onChange}
                                                  hasClear={true}
                                                  placeholder={"msg id"}></TextInput>
                                          </>
                                      )}
                                  </Field>
                              </Flex>
                              <br/>

                              <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                    gap={{s: 10, m: 6}}>
                                  <Field name={"originalMsgId"}>
                                      {props => (
                                          <>
                                              <TextInput
                                                  id={"originalMsgId"}
                                                  name={props?.input.name}
                                                  value={props.input.value}
                                                  onChange={props?.input.onChange}
                                                  hasClear={true}
                                                  placeholder={"original msg id"}></TextInput>
                                          </>
                                      )}
                                  </Field>
                              </Flex>
                              <br/>

                              <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                    gap={{s: 10, m: 6}}>
                                  <Field name={"rootTag"}>
                                      {props => (
                                          <>
                                              <TextInput
                                                  id={"rootTag"}
                                                  name={props?.input.name}
                                                  value={rootTagSelected}
                                                  onKeyPress={() => setOpenSelectTag(true)}
                                                  placeholder={"set root tag"}></TextInput>
                                          </>
                                      )}
                                  </Field>
                              </Flex>
                              <br/>

                              <Flex alignItems={"center"} justifyContent={"center"} alignItems={"center"}
                                    gap={{s: 10, m: 6}}>
                                  <Field name={"content"}>
                                      {props => (
                                          <>
                                              <Label theme={contentFile.length > 0 ? "info" : "danger"}
                                                     type={contentFile.length > 0 ? "close" : ""}
                                                     onCloseClick={() => {
                                                         if (window.confirm("Очистить контент")) {
                                                             setContentFile("");
                                                             inputContentFile = createRef();
                                                         }
                                                     }}>Контент</Label>
                                              <Button selected={contentFile.length > 0}
                                                      onClick={() => setOnDownloadContentFile(true)}
                                              >Открыть форму</Button>
                                              <br></br>
                                              <input
                                                  style={{display: 'none'}}
                                                  ref={inputContentFile}
                                                  type="file"
                                                  onChange={(e) => showFile(e)}
                                              />

                                              <Button selected={contentFile.length > 0}
                                                      onClick={openContentFile}>Загрузить файлом</Button>
                                          </>
                                      )}
                                  </Field>
                              </Flex>
                              <br/>

                              <Flex alignItems={"flex-end"} justifyContent={"flex-end"} alignItems={"flex-end"}
                                    gap={{s: 10, m: 6}}>
                                  <Button onClick={() => setOpenDialogSetAttach(true)}>Добавить вложение</Button>
                              </Flex>
                              <br/>
                              <div key={pageVersion}>
                                  {attachList()}
                              </div>
                              <br/>
                              <Flex alignItems={"flex-end"} justifyContent={"flex-end"} alignItems={"flex-end"}
                                    gap={{s: 10, m: 6}}>
                                  <Button view={"action"} type={"submit"}>Добавить запрос</Button>
                              </Flex>
                          </form>
                      )}/>
            )}
        </div>
    );
}