import metadata from "./../metadata.json"

const devUrl = {
    vsList: "http://localhost:8080/api/vs/list",
    addVs: "http://localhost:8080/api/vs/add_va",
    getRequestList: "http://localhost:8080/api/get_request/list",
    sendRequestList: "http://localhost:8080/api/send_request/list",
    getResponseList: "http://localhost:8080/api/get_response/list",
    createRequest: "http://localhost:8080/api/get_request/create",
    createResponse: "http://localhost:8080/api/get_response/create",
}
const resUrl = {
    vsList: "./api/vs/list",
    addVs: "./api/vs/add_va",
    getRequestList: "./api/get_request/list",
    sendRequestList: "./api/send_request/list",
    getResponseList: "./api/get_response/list",
    createRequest: "./api/get_request/create",
    createResponse: "./api/get_response/create",
}

export function UrlProperty() {
    if (metadata.tag === "dev") {
        return devUrl;
    } else if (metadata.tag === "res") {
        return resUrl;
    }
}