import {useEffect, useState} from "react";
import {Button, Container, Flex, Pagination, Table, useToaster, Text} from "@gravity-ui/uikit";
import axios from "axios";
import {UrlProperty} from "../../config/UrlProperty";
import moment from "moment";
import ViewGetResponse from "./ViewGetResponse";
import {Heading1, SquarePlus} from "@gravity-ui/icons";

export default function ViewGetResponseList() {
    const [data, setData] = useState([]);
    const [pagination, setPagination] = useState({pageSize: 10, page: 0, total: 0});
    const [k, setK] = useState(0);
    const [isOpenWindow, setIsOpenWindow] = useState(false);
    const [idRequest, setIdRequest] = useState(0);
    const [reqData, setReqData] = useState({});

    const {add} = useToaster();
    useEffect(() => {
        getRequestList()
    }, [k]);

    const columnProperty = [
        {
            id: "id",
            name: "id",
            template: (item) => {
                return (
                    <Button view={"flat-info"} onClick={() => {
                        setReqData(item)
                        setIsOpenWindow(true);
                    }}>{item.id}</Button>
                )
            }
        },
        {
            id: "reqStatus",
            name: "Статус"
        },
        {
            id: "rootTag",
            name: "Root Tag"
        },
        {
            id: "msgId",
            name: "MSG ID"
        },
        {
            id: "originalMsgId",
            name: "Original MSG ID"
        },
        {
            id: "createTimestamp",
            name: "Время создания",
            template: item => {
                if (item?.createTimestamp) {
                    return moment(item?.createTimestamp, "YYYY MM DD HH:mm:ss").format("YYYY MM DD HH:mm:ss");
                } else {
                    return "";
                }

            }
        },
        {
            id: "update",
            name: "Обновление",
            template: item => {
                if (item?.update) {
                    return moment(item?.update, "YYYY MM DD HH:mm:ss").format("YYYY MM DD HH:mm:ss");
                } else {
                    return "";
                }

            }
        }

    ]

    const getRequestList = () => {
        axios.get(UrlProperty().getResponseList, {
            params: {
                page_size: pagination.pageSize,
                page_number: pagination.page === 0 ? 0 : (pagination.page - 1)
            }
        }).then(r => {
            setData(r.data.content)
        }).catch(e => {
            add({
                theme: "danger",
                title: "Failed get GetRequest list", content: e.response.data
            })
        })
    }

    return (<>
        <ViewGetResponse
            open={isOpenWindow}
            setOpen={setIsOpenWindow}
            rowData={reqData}
        />

        <Container>
            <Text variant={"header-1"}>GetResponse List</Text>
            <Text variant={"body-2"}>
                <Table data={data} columns={columnProperty}></Table>
            </Text>
            <br></br>
            <Flex alignItems={"center"} gap={{s: 10, m: 6}}>
                <Pagination compact={true}
                            pageSize={pagination.pageSize}
                            page={pagination.page + 1}
                            total={pagination.total}
                            onUpdate={(p, ps) => {
                                setPagination({total: pagination.total, page: p, pageSize: ps})
                                setK(k + 1)
                            }}/>
            </Flex>
        </Container>
    </>)
}