import NavigateBar from "./toolbar/NavigateBar"
import {Card, Flex, ThemeProvider, ToasterProvider} from "@gravity-ui/uikit";
import {BrowserRouter, Route, Routes} from "react-router-dom";
import VsAddNew from "./forms/vs/VsAddNew";
import ViewVs from "./forms/vs/ViewVs";
import logo from "./img/logo.jpg"
import ViewGetRequestList from "./forms/get_request/ViewGetRequestList";
import NewRequest from "./forms/add_forms/newRequest";
import ViewGetResponseList from "./forms/get_response/ViewGetResponseList";
import ViewSendRequestList from "./forms/send_request/ViewSendRequestList";


function App() {

    const homePage = () => {
        return (
            <>
                <Card view={"outlined"} theme={"warning"}><h1 align={"center"}>Эмулятор СМЭВ3</h1>
                    <Flex alignItems={"center"} justifyContent={"center"}>
                        <img src={logo}/>
                    </Flex>
                </Card>
            </>
        )
    }

    return (
        <>
            <ThemeProvider theme="light">
                <NavigateBar/>
            </ThemeProvider>
            <br></br>
            <ToasterProvider>
                <Flex alignItems={"center"} justifyContent={"center"}>
                    <BrowserRouter>
                        <Routes>
                            <Route path={""} element={homePage()}/>
                            <Route path="vs_add" element={<VsAddNew/>}/>
                            <Route path="vs_view" element={<ViewVs/>}/>
                            <Route path="get_request" element={<ViewGetRequestList/>}/>
                            <Route path="get_response" element={<ViewGetResponseList/>}/>
                            <Route path="send_request" element={<ViewSendRequestList/>}/>
                            <Route path="add_request" element={<NewRequest/>}/>
                        </Routes>
                    </BrowserRouter>
                </Flex>
            </ToasterProvider>
        </>
    );
}

export default App;