import {Button, Checkbox, Container, Flex, Pagination, RadioGroup, Table} from "@gravity-ui/uikit";
import {useEffect, useState} from "react";
import axios from "axios";
import {UrlProperty} from "../../config/UrlProperty";
import VsAddNew from "./VsAddNew";

export default function ViewVs(props) {
    const [vsList, setVsList] = useState({});
    const [pagination, setPagination] = useState({pageSize: 10, page: 0, total: 0});
    const [k, setK] = useState(0);
    const [openAddVs, setOpenAddVs] = useState(false);
    useEffect(() => {
        getVsList();
    }, [k, openAddVs]);

    function getVsList() {
        axios.get(UrlProperty().vsList, {
            params: {
                page_size: pagination.pageSize,
                page_number: pagination.page === 0 ? 0 : (pagination.page - 1)
            }
        }).then(
            r => {
                setVsList(r.data.content);
                setPagination({
                    page: r.data.number,
                    pageSize: r.data.pageable.pageSize,
                    total: r.data.totalElements
                })
                console.log(pagination)
            }
        ).catch(e => {
            console.log(UrlProperty().vsList, e)
        })
    }


    const columnProperty = [
        {
            id: "selected",
            name: "",
            template: item => {
                return (<Checkbox checked={item.rootTag === props.rootTagSelected}
                                  onUpdate={() => props.setRootTagSelected(item.rootTag)} size="m"

                ></Checkbox>)
            }
        },
        {
            id: "id",
            name: "id"
        },
        {
            id: "rootTag",
            name: "Root Tag"
        },
        {
            id: "mnemonic",
            name: "Мнемоника"
        },
        {
            id: "mnemonicDesc",
            name: "Описание мнемоники"
        },
        {
            id: "description",
            name: "Описание ВС"
        }
    ]

    return (
        <>
            <Container>
                <Table data={vsList} columns={columnProperty}></Table>
                <br></br>
                <Flex alignItems={"center"} gap={{s: 10, m: 6}}>
                    <Pagination compact={true}
                                pageSize={pagination.pageSize}
                                page={pagination.page + 1}
                                total={pagination.total}
                                onUpdate={(p, ps) => {
                                    setPagination({total: pagination.total, page: p, pageSize: ps})
                                    setK(k + 1)
                                }}/>
                    <Button view={"outlined-info"} onClick={() => props.setOpenSelectTag(false)}>Ok</Button>
                    <Button view={"outlined-info"} onClick={() => setOpenAddVs(true)}>Добавить ВС</Button>
                </Flex>
            </Container>

            <VsAddNew open={openAddVs} setOpen={setOpenAddVs}/>
        </>
    )
}